import React,{useEffect, useState} from 'react';
import moment from 'moment';
import 'moment/locale/ko';
import './App.css';

function App() {
  const nowTime = moment().format('YYYYMMDD HH:mm:ss')
  
  const [pageTime,pageTimeChange] = useState(moment().format('YYYY년 MM월 DD일  HH시mm분ss초'));
  useEffect(function(){
    setInterval(function(){//1초마다 state 변경해서 컴포넌트 갱신
      // console.log('1s');
      pageTimeChange(moment().format('YYYY년 MM월 DD일  HH시mm분ss초'));
    },1000)  
  },[pageTime])  
  const [date1YYYY,date1YYYYChange] = useState('');
  const [date1MM,date1MMChange] = useState('');
  const [date1DD,date1DDChange] = useState('');

  const [date2YYYY,date2YYYYChange] = useState('');
  const [date2MM,date2MMChange] = useState('');
  const [date2DD,date2DDChange] = useState('');

  const [finalCount,finalCountChange] = useState('');
  
  function countDate(){    
    if(date1YYYY == "" || null || undefined || 0 || NaN){
      alert('모든 칸을 채워주세요.')
    }  
    else if(date2YYYY == "" || null || undefined || 0 || NaN){
      alert('모든 칸을 채워주세요.')
    }  
    else if(date1MM == "" || null || undefined || 0 || NaN){
      alert('모든 칸을 채워주세요.')
    }  
    else if(date2MM == "" || null || undefined || 0 || NaN){
      alert('모든 칸을 채워주세요.')
    }  
    else if(date1DD == "" || null || undefined || 0 || NaN){
      alert('모든 칸을 채워주세요.')
    }  
    else if(date2DD == "" || null || undefined || 0 || NaN){
      alert('모든 칸을 채워주세요.')
    }  
    else if(date1YYYY>2090){
      alert('연도는 2090을 넘을수 없습니다.');
    }
    else if(date1YYYY<1900){
      alert('연도는 1900보다 작을수 없습니다.');
    }
    else if(date1MM>12){
      alert('월은 1월부터 12월까지만 입력가능합니다.')
    }
    else if(date1MM<1){
      alert('월은 1월부터 12월까지만 입력가능합니다.')
    }
    else if(date1DD>31){
      alert('일은 1일부터 31일까지만 입력가능합니다.')
    }
    else if(date1DD<1){
      alert('일은 1일부터 31일까지만 입력가능합니다.')
    }
    
    else if(date2YYYY>2090){
      alert('연도는 2090을 넘을수 없습니다.');
    }
    else if(date2YYYY<1900){
      alert('연도는 1900보다 작을수 없습니다.');
    }
    else if(date2MM>12){
      alert('월은 1월부터 12월까지만 입력가능합니다.')
    }
    else if(date2MM<1){
      alert('월은 1월부터 12월까지만 입력가능합니다.')
    }
    else if(date2DD>31){
      alert('일은 1일부터 31일까지만 입력가능합니다.')
    }
    else if(date2DD<1){
      alert('일은 1일부터 31일까지만 입력가능합니다.')
    }

    

    else{
      const countdate1 = new Date(date1YYYY,date1MM,date1DD);
      const countdate2 = new Date(date2YYYY,date2MM,date2DD);
      const minusCount = countdate2.getTime() - countdate1.getTime();
      finalCountChange(minusCount / 1000 / 60 / 60 / 24);   
      console.log(finalCount);
    }    
  };
  return (
    <div className="App">
      <div className='container'>
        <h1>날짜 계산기</h1>
        <h2>지금은<br/>{pageTime} (GMT+9) 입니다.</h2>
        <div className='inner'>
          <form className='date_form'>
            <div className='top'>
              <input type="number" min="0" max="9999" maxLength="4" placeholder='연도' value={date1YYYY} onChange={(e)=>{date1YYYYChange(e.target.value)}}/>년
              <span className='blank'></span>
              <input type="number" maxLength="2" placeholder='월' value={date1MM} onChange={(e)=>{date1MMChange(e.target.value)}}/>월
              <span className='blank'></span>
              <input type="number" maxLength="2" placeholder='일' value={date1DD} onChange={(e)=>{date1DDChange(e.target.value)}}/>일
              <span className='to'>부터</span>
            </div>                        
            <div className='bottom'>
              <input type="number" maxLength="4" placeholder='연도' value={date2YYYY} onChange={(e)=>{date2YYYYChange(e.target.value)}}/>년
              <span className='blank'></span>
              <input type="number" maxLength="2" placeholder='월' value={date2MM} onChange={(e)=>{date2MMChange(e.target.value)}}/>월
              <span className='blank'></span>
              <input type="number" maxLength="2" placeholder='일' value={date2DD} onChange={(e)=>{date2DDChange(e.target.value)}}/>일
              <span className='from'>까지는</span>
            </div>            
            
            <button type='button' className='btn_count' onClick={countDate}>계산하기</button>
          </form>                        
                                    
          <div className='result'>
            <p>{finalCount-1}일 (시작일 포함{finalCount}일) 입니다!</p>
          </div>
        </div>        
      </div>
      
    </div>
  );
}

export default App;
